/* 
0 - 600px:    Phone
600 - 900px:  Tablet portrait
900 - 1200px: Tablet landscape
1200 - 1800px
1800px + :    Big desktop

//Breakpoints
-phone
-tab-port
-tab-land
-big-desktop
*/
@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 37.5625em) and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 56.3125em) and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

.container {
  height: 100vh;
}

.app {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "profile stats" "profile stats";
  width: 60%;
  gap: 15px;
}

.card-profile {
  grid-area: profile;
  display: grid;
  grid-template-rows: 3fr 1fr;
  grid-template-columns: 1fr;
  grid-template-areas: "top" "bottom";
  background: #1c1f4a;
  border-radius: 10px;
}
.card-profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background-image: linear-gradient(to bottom right, #5847eb, #6f76c8);
  border-radius: 10px;
  color: white;
}
.card-profile-top-image img {
  border: 3px solid white;
  border-radius: 50%;
}
.card-profile-top-name h1 {
  font-weight: 300;
  font-size: 3rem;
}
.card-profile-top-name span {
  font-size: 1rem;
  font-weight: 300;
}
.card-profile-bottom {
  grid-area: bottom;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2rem;
}
.card-profile-bottom button {
  background-color: transparent;
  border: none;
  outline: none;
  margin: 0.4rem 0rem;
  color: #6f76c8;
}

.stats {
  grid-area: stats;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "card card card" "card card card";
  gap: 15px;
}

.card {
  display: grid;
  grid-template-rows: 1fr 4fr;
  grid-template-columns: 1fr;
  grid-template-areas: "back" "stats";
}
.card-back {
  grid-area: back;
  background: transparent;
}
.card-front {
  background: #1c1f4a;
  color: white;
}
#work {
  background: #ff8c66;
}

#play {
  background: #56c2e6;
}

#study {
  background: #ff5c7c;
}

#exercise {
  background: #4acf81;
}

#social {
  background: #7536d3;
}

#self {
  background: #f1c65b;
}